<template>
  <div class="swiper-wrapper">
    <lu-loading v-if="!sliders.length"/>
    <lu-slider
      :data = "sliders"
      :direction="direction"
      :loop="loop"
      :interval="interval"
      :autoplay="autoplay"
      v-if="sliders.length"
    >
      <swiper-slide v-for="(item, index) in sliders" :key="index">
        <a href="##" class="slider-link">
          <img :src="item.picUrl" alt="" class="slider-img">
        </a>
      </swiper-slide>
    </lu-slider>
  </div>
</template>

<script>
  import LuSlider from '@/base/slider';
  import {swiperSlide} from 'vue-awesome-swiper';
  import {sliderOptions} from './config.js';
  import {getHomeSlider} from '@/api/home.js';
  import LuLoading from '@/base/loading';
  export default {
    name: 'HomeSlider',
    components: {
      LuSlider,
      swiperSlide,
      LuLoading
    },
    data () {
      return {
        // 滚动方向-水平
        direction: sliderOptions.direction,
        // 无缝滚动
        loop: sliderOptions.loop,
        // 滚动时间间隔
        autoplay: sliderOptions.autoplay,
        interval: sliderOptions.interval,
        sliders: [] // 数据通过ajax获取
      };
    },
    created () {
      this.getSliders();
    },
    methods: {
      update() {
        return this.getSliders();
      },
      getSliders() { // 接受轮播图数据
        return getHomeSlider().then(data => {
          this.sliders = data;
        });
      }
    }
  };
</script>
<style lang="scss" scoped>
  .swiper-wrapper{
    width: 100%;
    height: 183px;
  }
  .slider-link{
    display: block;
  }
  .slider-link,
  .slider-img{
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
</style>



