import axios from 'axios';
import {SUCCESS_CODE, TIME_OUT, JSONP_OPTIONS} from './config.js';
import jsonp from '@/assets/js/jsonp.js';
// home 页的脚本逻辑

// 打乱数组顺序的函数
const shuffle = (arr) => {
  const arrLength = arr.length;
  let i = arrLength;
  let rndNum;
  while (i--) {
    if (i !== (rndNum = Math.floor(Math.random() * arrLength))) {
      [arr[i], arr[rndNum]] = [arr[rndNum], arr[i]];
    }
  }
  return arr;
};

// 获取幻灯片的数据--ajax
export const getHomeSlider = () => {
  return axios.get('static/slide.json', {
    timeout: TIME_OUT
  }).then(res => {
    if (res.data.code === SUCCESS_CODE) {
      let sliders = res.data.slider;
      const slide = [sliders[Math.floor(Math.random() * sliders.length)]];
      // filter过滤器，当math.random>=0.5为真则保留，否则剔除,返回的数组传入shuffle 函数中
      sliders = shuffle(sliders.filter(() => Math.random() >= 0.5));
      //  判断sliders数组，如果sliders里全被剔除了执行下面的函数
      if (sliders.length === 0) {
        sliders = slide;
      }
      // 返回筛选到的数据
      return sliders;
    } else {
      throw new Error('没有成功获取到数据');
    }
  }).catch(err => {
    if (err) {
      console.log(err);
    }
    // 出现错误的时候图片替代
    return [{
      urlLink: 'http://www.baidu.com',
      picUrl: require('@/assets/img/404.png')
    }];
  });
};
// 获取热门推荐数据 -- jsonp
export const getHomeRecommend = (page = 1, psize = 20) => {
  const url = 'https://ju.taobao.com/json/tg/ajaxGetItemsV2.json';
  const params = {
    page,
    psize,
    type: 0,
    frontCatId: ''
  };
  return jsonp(url, params, JSONP_OPTIONS).then(res => {
    console.log(res);
    if (res.code === '200') {
      return res;
    } else {
      throw new Error('没有成功获取到数据');
    }
  }).catch(err => {
    console.log(err);
  });
};



