<template>
  <div class="home">
    <!--头部容器-->
    <header class="g-header-container">
      <!--头部组件 ./header-->
      <home-header :class="{'hearder-transition': isHearderTransition}" ref="header"/>
    </header>
    <!--滚动条组件 base/scroll 首页的主体部分-->
    <lu-scroll
      ref="scroll"
      :data="recommends"
      pullDown
      pullUp
      @pull-down="pullToRefresh"
      @pull-up="pullToLoadMore"
      @scroll-end="scrollEnd"
      @scroll="scroll"
      @pull-down-transition-end="pullDownTransitionEnd"
    >
      <!--轮播图组件 基础组件封装在base下，在home下第二次封装-->
      <home-slider ref="sliders" />
      <!--导航面板组件 ./nav-->
      <home-nav/>
      <!--商品展示组件-->
      <home-recommend @loaded="getRecommend" ref="recommend"/>
    </lu-scroll>
    <!--底部导航条-->
    <div class="g-backtop-container">
      <lu-backtop :visible="isBacktopVisible" @backtop="backToTop"/>
    </div>

    <router-view></router-view>
  </div>
</template>

<script>
  import HomeHeader from './header';
  import HomeSlider from './slider';
  import HomeNav from './nav';
  import HomeRecommend from './recommend';
  import LuScroll from '@/base/scroll';
  import LuBacktop from '@/base/backtop';
  import {HEADER_TRANSITION_HEIGHT} from './config.js';
  export default {
    name: 'home',
    components: {
      HomeHeader,
      HomeSlider,
      LuScroll,
      HomeNav,
      HomeRecommend,
      LuBacktop
    },
    data () {
      return {
        recommends: [],
        isBacktopVisible: false,
        isHearderTransition: false
      };
    },
    methods: {
      updateScroll() {
      },
      getRecommend(recommends) {
        this.recommends = recommends;
      },
      pullToRefresh(end) {
        this.$refs.sliders.update().then(end);
      },
      pullToLoadMore(end) {
        this.$refs.recommend.update().then(end).catch(err => {
          if (err) {
            console.log(err);
          }
          end();
          // 接下来可以处理没有更多数据的情况
        });
      },
      scrollEnd(translate, scroll, pulling) {
        if (!pulling) {
          this.changeHearderStatus(translate);
        }
        this.isBacktopVisible = translate < 0 && -translate > scroll.height;
      },
      backToTop() {
        // if (this.$refs.scroll) {
        // this.$refs.scroll.scrollToTop();
        // }   的语法糖：
        this.$refs.scroll && this.$refs.scroll.scrollToTop();
      },
      scroll(translate) {
        this.changeHearderStatus(translate);
      },
      pullDownTransitionEnd() {
        this.$refs.header.show();
      },
      changeHearderStatus(translate) {
        if (translate > 0) {
          this.$refs.header.hide();
          return;
        }
        // this.$refs.header.show(); 此处显示头部会导致还未刷新完成头部就出现，因此提到pullDownTransitionEnd中
        this.isHearderTransition = -translate > HEADER_TRANSITION_HEIGHT;
      }
    }
  };
</script>

<style lang="scss" scoped>
@import "~@/assets/scss/mixins";
  .home{
    overflow: hidden;
    width: 100%;
    height: 100%;
    background-color: $bgc-theme;
  }
</style>



