<template>
    <swiper :options="swiperOptions" :key="keyId">
      <slot></slot>
      <div class="swiper-pagination" v-if="pagination" slot="pagination"></div>
    </swiper>
</template>

<script>
  import {swiper} from 'vue-awesome-swiper';
  export default {
    name: 'Luslider',
    components: {
      swiper
    },
    props: {
      direction: {
        type: String,
        default: 'horizontal',
        validator(value) {
          return [
            'horizontal',
            'vertical'
          ].indexOf(value) > -1;
        }
      },
      interval: {
        type: Number,
        default: 3000,
        validator(value) {
          return value >= 0;
        }
      },
      loop: {
        type: Boolean,
        default: true
      },
      pagination: {
        type: Boolean,
        default: true
      },
      autoplay: {
        type: Boolean,
        default: true
      },
      data: {
        type: Array,
        default() {
          return [];
        }
      }
    },
    data () {
      return {
        keyId: Math.random()
      };
    },
    watch: {
      data(newData) {
        if (newData.length === 0) {
          return;
        }
        this.swiperOptions.loop = newData.length <= 1 ? false : this.loop;
        this.keyId = Math.random();
      }
    },
    created () {
      this.init();
    },
    methods: {
      init() {
        this.swiperOptions = {
          watchOverflow: true,
          direction: this.direction,
          interval: this.interval,
          slidesPerView: 1,
          autoplay: this.autoplay,
          loop: this.data.length <= 1 ? false : this.loop,
          pagination: {
            el: '.swiper-pagination'
          }
        };
      }
    }
  };
</script>
<style lang="scss" scoped>
  .swipper-container{
    width: 100%;
    height: 100%;
  }
</style>



