<template>
  <lu-navbar class="header" v-show="visible">
    <i class="iconfont icon-scan" slot="left"></i>
    <lu-search-box
      slot="center"
      placeholder="情人节巨献"
      @query="getQuery"
      fake
      @click.native="goToSearch"
    ></lu-search-box>
    <i class="iconfont icon-msg" slot="right"></i>
  </lu-navbar>
</template>

<script>
  import LuNavbar from '@/base/navbar';
  import LuSearchBox from '@/base/search-box';
  export default {
    name: 'HomeHeader',
    components: {
      LuNavbar,
      LuSearchBox
    },
    data() {
      return {
        visible: true
      };
    },
    methods: {
      show() {
        this.visible = true;
      },
      hide() {
        this.visible = false;
      },
      getQuery(query) {
        console.log(query);
      },
      goToSearch() {
        this.$router.push('/search');
      }
    }
  };
</script>
<style lang="scss" scoped>
@import '~@/assets/scss/mixins';
.header{
  &.mine-navbar{
    background-color: transparent;
    transition: background-color 0.2s;
    //background-color: $header-bgc-translucent;
  }
  .iconfont{
    color: $icon-color-default;
    font-size: $icon-font-size;
  }
  &.hearder-transition{
    background-color: $header-bgc-translucent;
  }
}
</style>



