<template>
    <div class="nav">
      <ul class="nav-list">
        <li class="nav-item" v-for="(item, index) in navs" :key="index">
          <a href="##" class="nav-link">
            <img :src="item.picUrl" class="nav-pic">
            <span>{{ item.text }}</span>
          </a>
        </li>
      </ul>
    </div>
</template>

<script type="text/ecmascript-6">
  import {navItems} from './config.js';
  export default {
    name: 'HomeNav',
    created () {
      this.navs = navItems;
    }
  };
</script>
<style lang="scss" scoped>
  @import "~@/assets/scss/mixins";
  .nav{
    width: 100%;
    padding-top: 15px;
    background-color: #fff;
    &-list{
      display: flex;
      flex-wrap: wrap;
    }
    &-item{
      width: 20%;
      margin-bottom: 15px;
    }
    &-link{
      @include flex-center(column);
    }
    &-pic{
      width: 60%;
      margin-bottom: 6px;
    }
  }

</style>



